.tab {
  &-navigation {
    margin: 35px auto;
    border-top: 1px solid rgba(137, 215, 215, .5);
    border-bottom: 1px solid rgba(137, 215, 215, .5);
    overflow: auto;
    @include respond-to(md) {
      border: none;
      margin: 0 0 16px;
    }
    &::-webkit-scrollbar {
      display: none;
    }
  }
  &-box {
    display: none;
  }
  &-list {
    flex-wrap: nowrap;
    justify-self: center;
    @include respond-to(sm) {
      flex-direction: column;
      align-items: flex-start;
    }
    li {
      width: calc(100% / 3);
      @include respond-to(sm) {
        width: auto;
      }
    }
  }
  &-item {
    position: relative;
    @include fsb;
    font-size: 20px;
    line-height: 28px;
    color: $main-blue;
    padding: 22px 0;
    display: block;
    text-align: center;
    text-transform: uppercase;
    overflow: hidden;
    transition: all .3s ease-in;
    @include respond-to(md) {
      font-size: 16px;
      padding: 8px 0;
    }
    @include respond-to(sm) {
      text-align: left;
    }
    &:before,
    &:after {
      content: '';
      opacity: 0;
      transform: scalex(0);
      position: absolute;
      right: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: rgba(137, 215, 215, 1);
      transition: all .3s ease-in;
      margin: auto;
      z-index: 1;
    }
    &:before,
    &:before {
      top: 0;
      @include respond-to(md) {
        display: none;
      }
    }
    &:after,
    &:after {
      bottom: 0;
    }
    &.active,&:hover {
      color: $main-blue;
    }
    &.active:after,
    &.active:before,
    &:hover:after,
    &:hover:before {
      transform: scale(1);
      opacity: 1;
    }
  }
}
