.header {
  position: fixed;
  width: 100%;
  padding: 16px 0;
  background-color: $default-color;
  z-index: 9;
  &__logo {
    width: calc(100% / 3);
    img {
      max-height: 48px;
      @include respond-to(sm-767){
        max-height: 35px;
      }
    }
  }
  &__logo-descr {
    color: $main-blue;
    @include fm;
    font-size: 16px;
    line-height: 20px;
    padding-left: 22px;
    margin-left: 22px;
    border-left: 2px solid $main-blue;
  }
  &__flag {
    width: calc(100% / 3);
    img {
      margin: auto;
    }
  }
  &__nav {
    width: calc(100% / 3);
  }
}
.hidden-tablet {
  @include respond-to(lg-1200){
    display: none;
  }
}
.hidden-mob {
  @include respond-to(xs-600){
    display: none;
  }
}
.hidden-sm {
  @include respond-to(sm){
    display: none;
  }
}
.visible-mob {
  display: none;
  @include respond-to(xs-600){
    display: block;
  }
}
.menu-button {
  height: 28px;
  width: 28px;
  position: relative;
  z-index: 101;
  display: block;
  margin-left: 40px;
  @include respond-to(sm-767){
    margin-left: 22px;
  }
  .lines {
    border-radius: 8px;
    height: 4px;
    left: 0;
    position: absolute;
    width: 100%;
    background-color: $main-blue;
    top: 50%;
    transition: height .1s;
    @include respond-to(sm-767) {
      height: 2px;
    }
    &::before,
    &::after {
      content: '';
      background-color: $main-blue;
      border-radius: 8px;
      height: 4px;
      right: 0;
      position: absolute;
      width: 100%;
      background-color: $main-blue;
      transition: all .5s;
      @include respond-to(sm-767) {
        height: 2px;
      }
    }
    &::after {
      top: -9px;
      @include respond-to(sm-767) {
        top: -10px
      }
    }
    &::before {
      bottom: -9px;
      @include respond-to(sm-767) {
        bottom: -10px
      }
    }
  }
  &:hover {
    .lines::before,
    .lines::after {
      width: 80%;
      
    }
    .lines,
    .lines::before,
    .lines::after {
      background-color: $yellow;
    }
  }
}