@each $prop in (center, flex-start, flex-end) {
    .align-items-#{$prop} {
      align-items: $prop !important;
    }
}
@each $prop in (center, space-between, flex-end) {
    .justify-content-#{$prop} {
      justify-content: $prop !important;
    }
}
@each $size in (0, 2, 4, 8, 16, 22, 24) {
  @each $val in ((t, top), (b, bottom), (l, left), (r, right)) {
    .m-#{$size} {
      margin: #{$size}px !important;
    }
    .m#{nth($val, 1)}-#{$size} {
      margin-#{nth($val, 2)}: #{$size}px !important;
    }
    .p#{nth($val, 1)}-#{$size} {
      padding-#{nth($val, 2)}: #{$size}px !important;
    }
  }
}
.mt-auto {
  margin-top: auto;
}
.font-700 {
    font-weight: 700 !important;
}
.in-blue {
  color: $main-blue !important;
}
.in-white {
  color: white !important;
}
.center-in-absolute {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.text-sm {
  font-size: 12px;
  line-height: 15px;
}
.text-body {
  font-size: 16px;
  line-height: 22px;
  @include respond-to(lg-1260) {
    font-size: 14px;
    line-height: 20px;
  }
  @include respond-to(sm-767) {
    font-size: 14px;
    line-height: 20px;
  }
}
.text-body--bg {
  font-size: 20px;
  line-height: 26px;
  @include respond-to(lg-1260) {
    font-size: 16px;
    line-height: 22px;
  }
  @include respond-to(sm-767) {
    font-size: 14px;
    line-height: 20px;
  }
}
.text-subheader {
  font-size: 24px;
  line-height: 32px;
  @include respond-to(lg-1260) {
    font-size: 18px;
    line-height: 24px;
  }
  @include respond-to(sm-767) {
    font-size: 14px;
    line-height: 20px;
}
}
.text-subheader--bg {
  font-size: 30px;
  line-height: 42px;
  @include respond-to(lg-1260) {
    font-size: 24px;
    line-height: 32px;
  }
  @include respond-to(sm-767) {
    font-size: 16px;
    line-height: 22px;
  }
}
.relative {
  position: relative;
}
.index-top {
  z-index: 1;
}