.mainMenu {
    position: fixed;
    right: 0;
    top: 0;
    z-index: 9999;
    min-height: 100vh;
    width: 330px;
    text-align: center;
    opacity: 0;
    transition: all .5s;
    transform: translateX(100%);
    display: flex;
    flex-direction: column;
    background-color: $gray;
    overflow: hidden;
}
.mainMenu.is-active {
    opacity: 1;
    transform: translateX(0)
}
.mainMenu.is-active ul li {
    opacity: 1;
    transform: translateX(0);
    transition: all 1s cubic-bezier(.68, -.55, .265, 1.55);
}
.mainMenu .logo-block {
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
    margin-bottom: 20px;
    img {
        margin: auto;
        @include respond-to(sm-767) {
            max-width: 85px;
        }
    }
}
.mainMenu ul {
    display: block;
    vertical-align: middle;
    width: 100%;
    padding-top: 100px
}
@media only screen and (max-height:400px) {
    .mainMenu ul {
        padding-top: 20px
    }
}
.mainMenu li {
    padding: 6px 0;
    transition: all .5s .5s;
    opacity: 0;
    @include respond-to(sm-767) {
       padding: 6px 0;
    }
}
.mainMenu li:nth-child(odd) {
    transform: translateX(30%)
}
.mainMenu li:nth-child(even) {
    transform: translateX(-30%)
}
.mainMenu li:last-child {
    transform: none
}
.mainMenu a {
    display: inline-block;
    font-size: 16px;
    font-weight: 700;
}
.close-menu {
    position: absolute;
    left: 20px;
    top: 20px;
    font-size: 25px;
    cursor: pointer;
    @include anim;
    color: $main-blue;
    &:hover{
      transform: rotate(180deg);
    }
}
.soc-nav {
    position: relative;
    padding: 20px 0; 
    margin-top: auto;
    .link {
        a {
            font-size: 16px;
            @include respond-to(sm-767) {
                font-size: 14px;
            }
        }
    }
}