.top__slider .swiper {
    max-height: 736px;
    @include respond-to(sm) {
        height: 240px;
    }
    img {
        width: 100%;
        height: 100%;
        @include respond-to(sm) {
            object-fit: cover;
            object-position: 0 50%;
        }
    }
    .swiper-wrapper {
        height: 100%;
    }
    .swiper-slide {
        position: relative;
        height: 100%;
    }
} 
.slider-navigation {
    font-size: 26px;
    &--blue {
        color: $main-blue;
    }
}
.swiper-prev {
    margin-right: 22px;
}
.swiper-prev:not(.swiper-button-disabled),
.swiper-next:not(.swiper-button-disabled) {
    cursor: pointer;
    @include anim;
    &:hover {
        color: $yellow;
    }
}
.swiper-button-disabled {
    cursor: default;
    opacity: .5;
}  
.swiper-pagination {
    text-align: center;
}
  
.swiper-pagination-bullet {
    width: 12px;
    height: 12px;
    background: transparent;
    border: 1px solid $main-blue;
    display: inline-block;
    vertical-align: middle;
    opacity: 1;
    &.swiper-pagination-bullet-active {
        background: $main-blue;
    }
}
.swiper-pagination--dark {
    .swiper-pagination-bullet {
        border: 1px solid $default-color;
        &.swiper-pagination-bullet-active {
            background: $default-color;
        }
    }
}
.slider-btn {
    position: absolute;
    bottom: 120px;
    right: calc((100% - 1230px) / 2);
    @include respond-to(lg-1200) {
        bottom: 40px;
        right: 16px;
    }
}
  