.play-btn {
    position: relative;
    width: 30px;
    height: 30px;
    background-color: $default-color;
    border-radius: 50%;
    font-size: 12px;
    color: $main-blue;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 22px;
    @include anim;
    .icon {
        position: relative;
        z-index: 2;
    }
    &::before {
        position: absolute;
        z-index: 0;
        left: 50%;
        top: 50%;
        transform: translateX(-50%) translateY(-50%);
        display: block;
        width: 30px;
        height: 30px;
        background: $main-blue;
        border-radius: 50%;
        animation: pulse-border 1500ms ease-out infinite;
    }
    &--lg {
        width: 100px;
        height: 100px;
        @include respond-to(tablet-1030) {
            width: 42px;
            height: 42px;
        }
        &::before {
            width: 100px;
            height: 100px;
            @include respond-to(tablet-1030) {
                width: 42px;
                height: 42px;
            }
        }
        .icon {
            font-size: 24px;
            @include respond-to(tablet-1030) {
                font-size: 16px;
            }
        }
    }
    &__wrap {
        &:hover {
            .play-btn {
                background-color: $main-blue;
                .icon {
                    color: $default-color;
                }
                &:before {
                    content: "";
                }
            }
        }
    }
    &--blue {
        border: 1px solid $main-blue;
        background-color: transparent;
        color: $main-blue;
    }
}
@keyframes pulse-border {
    0% {
      transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1);
      opacity: 1;
    }
    100% {
      transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1.5);
      opacity: 0;
    }
}