.main-wrap {
    padding-top: 80px;
    @include respond-to(sm-767){
        padding-top: 67px;
    }
}
.section {
    padding-top: 60px;
    padding-bottom: 60px;
    @include respond-to(lg-1260) {
        padding-top: 48px;
        padding-bottom: 48px;
    }
    @include respond-to(sm-767){
        padding-top: 24px;
        padding-bottom: 24px;
    }
}
.bg-ua {
    background: url(../images/ornament.svg) 50%/contain;
    background-color: $default-color;
    background-attachment: fixed;
}
//achiev start
.achiev {
    &__item {
        position: relative;
        @include respond-to(sm) {
            margin-bottom: 28px;
        }
        @include respond-to(sm-767) {
            margin-bottom: 24px;
        }
        &::after {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: linear-gradient(0deg, rgba(0, 0, 0, 0.5) 30%, rgba(255, 255, 250, 0) 60%);
        }
        &::before {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            border-width: 2px;
            border-style: solid;
            border-image: linear-gradient(#89d7d7, rgba(137, 215, 215, 0.25), rgba(137, 215, 215, 0.26), #89d7d7) 1 10%;
            z-index: 1;
        }
        .play-btn__wrap {
            z-index: 1;
        }
    }
    &__item--sm {
        margin-bottom: 28px;
        @include respond-to(sm-767) {
            margin-bottom: 24px;
        }
        .achiev__title {
            font-size: 64px;
            line-height: 77px;
            @include respond-to(lg-1260) {
                font-size: 50px;
                line-height: 60px;
            }
        }
    }
    &__info {
        position: absolute;
        bottom: 18px;
        left: 0;
        right: 0;
        text-align: center;
        z-index: 1;
        padding: 0 26px;
        @include respond-to(sm-767) {
            padding: 0 16px;
        }
    }
    &__img {
        img {
            width: 100%;
        }
    }
    &__title {
        color: $main-blue;
        font-size: 120px;
        line-height: 144px;
        @include fbl;
        @include respond-to(lg-1260) {
            font-size: 100px;
            line-height: 120px;
        }
        @include respond-to(sm-767) {
            font-size: 50px;
            line-height: 60px;
        }
    }
    &__descr {
        color: white;
        @include fm;
    }
}
.about-record {
    margin-left: auto;
    &__item {
        padding: 0 20px;
    }
    &__logo {
        min-height: 42px;
        margin-bottom: 22px;
        @include respond-to(lg-1260) {
            margin-bottom: 16px;
        }
        @include respond-to(sm-767) {
            min-height: 24px;
            img {
                max-width: 58px;
            }
        }
    }
    &__title {
        font-size: 40px;
        line-height: 42px;
        @include fb;
        color: $main-blue;
        @include respond-to(lg-1260) {
            font-size: 32px;
            line-height: 34px;
        }
        @include respond-to(sm-767) {
            font-size: 18px;
            line-height: 20px;
        }
    }
    &__time {
        font-size: 24px;
        line-height: 31px;
        @include fsb;
        color: $main-red;
        padding-bottom: 16px;
        @include respond-to(lg-1260) {
            font-size: 22px;
            line-height: 28px;
            padding-bottom: 4px;
        }
        @include respond-to(sm-767) {
            font-size: 16px;
            line-height: 22px;
        }
        &:last-child {
            padding-bottom: 0;
        }
    }
}
.bottom-dark-bg {
    &::after {
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.78) 38.64%, rgba(0, 0, 0, 0.47) 77.81%);
    }
}
.top-dark-bg {
    &::after {
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.47) 8.84%, rgba(0, 0, 0, 0.78) 24.21%, rgba(0, 0, 0, 0.47) 52.11%);
    }
}
.without-dark-bg {
    &::after {
        display: none;
    }
}
// feature start
.features {
    &__left {
        position: relative;
        background-image: url(../images/how-bg.jpg);
        background-size: cover;
        width: 60%;
        padding-left: calc((100% - 1260px + 30px) / 2);
        padding-top: 48px;
        @include respond-to(lg-1360) {
            padding-left: calc((100% - 1260px + 60px) / 2);
        }
        @include respond-to(lg-1260) {
            padding-left: 30px;
        }
        @include respond-to(sm) {
            width: 100%;
            padding-right: 30px;
            padding-top: 24px;
            background-attachment: fixed;
        }
        @include respond-to(sm-767) {
            padding-left: 20px;
            padding-right: 20px;
        }
        &::before {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: rgba(137, 215, 215, 0.7);
        }
        .page-title,
        .features__icons {
            position: relative;
            z-index: 1;
        }
    }
    &__slider {
        position: relative;
        padding-top: 48px;
        padding-left: 43px;
        padding-right: calc((100% - 1260px + 30px) / 2);
        width: 40%;
        background-color: $main-blue;
        @include respond-to(lg-1360) {
            padding-right: calc((100% - 1260px + 60px) / 2);
        }
        @include respond-to(lg-1260) {
            padding-right: 30px;
        }
        @include respond-to(sm) {
            width: 100%;
            padding: 24px 30px;
        }
        @include respond-to(sm-767) {
            padding-left: 20px;
            padding-right: 20px;
        }
        .swiper {
            padding-top: 82px;
            @include respond-to(sm) {
                padding-top: 0;
                padding-bottom: 42px;
            }
        }
    }
    &__slider-devider {
        position: absolute;
        right: 100%;
        width: 0;
        height: 0;
        border-top: 45px solid transparent;
        border-right: 45px solid $main-blue;
        border-bottom: 45px solid transparent;
        top: 140px;
        font-size: 46px;
        .icon {
            position: absolute;
            top: -24px;
            left: 12px;
        }
    }
    &__icons {
        max-width: 680px;
        @include respond-to(sm) {
            max-width: 100%;
        }
    }
    &__icon-item {
        width: 48%;
        text-align: center;
        margin-bottom: 48px;
        padding: 16px;
        @include anim;
        cursor: pointer;
        border-radius: 17px;
        @include respond-to(sm-767) {
            width: 50%;
            padding: 16px 8px;
            margin-bottom: 30px;
        }
        img {
            margin: auto;
            @include respond-to(sm-767) {
                max-height: 40px;
            }
        }
        &:hover {
            transform: scale(1.1);
        }
        &.active-features {
            box-shadow: 0px 1px 14px rgba(0, 0, 0, 0.27);
            transform: scale(1.1);
        }
    }
    &__descr {
        padding-top: 16px;
        @include fm;
        @include respond-to(sm-767) {
            font-size: 12px;
            line-height: 16px;
        }
    }
    &__info {
        @include fsb;
    }
}
.ornament-bg {
    position: absolute;
    background-image: url(../images/flower6.svg);
    background-size: contain;
    background-repeat: no-repeat;
    transition: transform 3.5s cubic-bezier(.68, -.55, .265, 1.55);
    z-index: 1;
    &--md {
        width: 200px;
        height: 170px;
        left: -3%;
        top: 0%;
    }
    &--xs {
        width: 120px;
        height: 103px;
        right: 25%;
        top: 35%;
        @include respond-to(sm) {
            right: auto;
            left: 0;
            top: 42%;
        }
    }
}
.flower-bg {
    position: absolute;
    background-image: url(../images/flower1.svg);
    background-size: contain;
    background-repeat: no-repeat;
    z-index: 1;
    transition: transform 4.5s, -webkit-transform 4.5s;
    &--xl {
        left: 5%;
        top: 20%;
        width: 350px;
        height: 300px;
        @include respond-to(sm) {
            right: 0%;
            top: 23%;
            width: 150px;
            height: 150px;
            left: auto;
        }
    }
    &--lg {
        right: 0;
        bottom: 15px;
        right: -90px;
        width: 300px;
        height: 259px;
    }
    &--md {
        width: 175px;
        height: 150px;
        left: 43px;
        bottom: 20%;
        @include respond-to(sm) {
            right: 0;
            left: auto;
            bottom: 35%;
        }
    }
    &--xs {
        width: 120px;
        height: 103px;
        right: -30px;
        top: 15%;
        @include respond-to(sm) {
            top: auto;
            bottom: 25px;
            width: 83px;
            height: 72px;
        }
    }
    &--xxs {
        width: 100px;
        height: 75px;
        left: 53%;
        top: 54%;
    }
}

// programms start
.programs-bg {
    position: relative;
    background-image: url(../images/programs-bg.jpg);
    background-color: $default-color;
    background-size: cover;
    background-attachment: fixed;
    &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.8);
    }
}
.programs {
    padding-top: 32px;
    &__info {
        max-width: 840px;
        margin: auto;
        padding-top: 24px;
        @include fsb;
        @include respond-to(xs-600){
            padding-top: 0px;
        }
        .text-body--bg {
            max-width: 60%;
            padding-right: 24px;
            @include respond-to(xs-600){
                max-width: 100%;
                padding-right: 0;
                padding-bottom: 16px;
                text-align: center;
            }
        }
        .btn__wrap {
            @include respond-to(xs-600){
                margin: auto;
            }
        }
    } 
}
.program-card {
    border: 1px solid $main-blue;
    height: 100%;
    @include respond-to(sm-767) {
        height: auto;
    }
    &__header {
        padding: 16px;
        background-color: $main-blue;
        @include fsb;
        min-height: 100px;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        flex-wrap: wrap;
        flex-direction: column;
        @include respond-to(lg-1260) {
           min-height: 80px;
        }
        @include respond-to(sm-767) {
            min-height: 50px;
        }
    }
    &__item {
        margin-bottom: 16px;
        &:last-child {
            margin-bottom: 0;
        }
        .icon {
            font-size: 12px;
            color: $main-blue;
            margin-right: 12px;
            margin-top: 4px;
        }
    }
    &__body {
        padding: 22px 26px 18px;
        height: calc(100% - 100px);
        display: flex;
        flex-direction: column;
        margin-top: auto;
        @include respond-to(lg-1260) {
            height: calc(100% - 80px);
            padding: 18px 22px;
        }
        @include respond-to(sm-767) {
            height: calc(100% - 50px);
        }
    }
    &__list {
        margin-bottom: 32px;
    }
    &__title {
        width: calc(100% - 24px);
    }
    &__btns {
        margin-top: auto;
        text-align: center;
        .btn__wrap {
            display: block;
            margin: 0 auto 16px;
        }
    }
}

//faq start
.faq-item {
    position: relative;
    background-color: $gray;
    border-radius: 17px;
    padding: 50px 70px;
    margin-bottom: 53px;
      @include respond-to(lg-1260) {
        padding: 24px 32px;
        margin-bottom: 24px;
      }
      @include respond-to(sm-767) {
        padding: 16px 18px;
        margin-bottom: 24px;
      }
    &:last-child {
        margin-bottom: 0;
    }
    &__title {
        width: 90%;
        cursor: pointer;
        @include respond-to(sm-767) {
            width: 75%;
        }
        &:hover {
            .symbol:before, .symbol:after {
                background-color: $yellow;
            }
        }
    }
    &__info {
        padding-top: 30px;
        @include respond-to(sm-767){
            padding-top: 22px;
        }
    }
}
.symbol {
    cursor: pointer;
    width: 30px;
    height: 30px;
    position: absolute;
    right: 70px;
    top: 90px;
    transform: translateY(-50%);
    text-align: center;
    line-height: 20px;
    @include respond-to(lg-1260){
        right: 32px;
        top: 48px;
        width: 18px;
        height: 18px;
    }
    @include respond-to(sm-767){
        right: 18px;
        top: 32px;
    }
  }
  .symbol:before, .symbol:after {
    background: $main-blue;
    content: '';
    position: absolute;
    height: 2px;
    width: 30px;
    top: 0;
    left: 0;
    @include respond-to(lg-1260){
        width: 18px;
        height: 1px;
    }
  }
  
  .symbol:after {
    transform: rotate(-90deg);
    transition: transform 0.1s ease;
  }
  .faq-item--open .symbol:after {
    transform: rotate(0deg);
  }
  // war start
  .bg-war {
    position: relative;
    background-image: url(../images/war-bg.jpg);
    background-color: $default-color;
    background-size: cover;
    background-attachment: fixed;
    &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: rgba(2, 2, 2, 0.75);
    }
  }
.about-war {
    @include fsb;
    @include respond-to(sm) {
        padding-bottom: 48px;
    }
    &__title {
        @include fb;
        font-size: 40px;
        line-height: 48px;
        color: $main-blue;
        padding-bottom: 16px;
        @include respond-to(lg-1260) {
            font-size: 24px;
            line-height: 32px;
        }
        @include respond-to(sm-767) {
            font-size: 18px;
            line-height: 22px;
        }
    }
    &__img {
        margin-bottom: 60px;
        @include respond-to(lg-1260) {
            margin-bottom: 36px;
        }
        img {
            width: 100%;
            @include respond-to(sm) {
                max-height: 400px;
                object-fit: cover;
            }
            @include respond-to(xs) {
                max-height: 180px;
            }
        }
        &::before {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            border-width: 2px;
            border-style: solid;
            border-image: linear-gradient(#89d7d7, rgba(137, 215, 215, 0.25), rgba(137, 215, 215, 0.26), #89d7d7) 1 10%;
            z-index: 1;
        }
        .play-btn__wrap {
            z-index: 1;
        }
    }
}
// feedback start

.feedback {
    background-color: $main-blue;
    padding-bottom: 200px;
    @include respond-to(md){
        padding-bottom: 48px;
    }
    @include respond-to(sm-767){
        padding-bottom: 24px;
    }
    &::after {
        content: '';
        background-image: url(../images/feedback-bottom.svg);
        width: 100%;
        height: 77px;
        background-size: cover;
        position: absolute;
        bottom: 60px;
        left: 0;
        @include respond-to(md){
            content: none;
        }
    }
    &__title-wrap {
        position: relative;
        margin-bottom: 45px;
        @include respond-to(sm){
            margin-bottom: 22px;
        }
        .page-title {
            padding-bottom: 0;
        }
        &::after {
            content: '';
            background-image: url(../images/feedback-top.svg);
            width: 71%;
            height: 64px;
            background-size: cover;
            position: absolute;
            bottom: -9px;
            left: 240px;
            @include respond-to(lg-1260) {
                width: 69%;
                left: 190px;
            }
            @include respond-to(md){
                width: 63%;
            }
            @include respond-to(sm){
               width: calc(100% - 151px);
               height: 20px;
               background-size: contain;
               bottom: 12px;
            }
            @include respond-to(xs){
                width: calc(100% - 90px);
                height: 30px;
                background-size: cover;
                bottom: -3px;
                left: 110px;
            }
        }
    }
    &__item {
        position: relative;
        &::before {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.64);
        }
        img {
            width: 100%;
        }
    }
    &__info {
        position: absolute;
        bottom: 15px;
        left: 0;
        right: 0;
        padding: 0 15px;
        text-align: center;
        z-index: 1;
    }
    &__title {
        @include fsb;
    }
    .swiper {
        @include respond-to(sm){
            padding-bottom: 42px;
        } 
    }
}
// packages start 
.packages-card {
    background: rgba(0, 0, 0, 0.42);
    border: 2px solid $main-blue;
    border-radius: 17px;
    padding: 32px 37px;
    height: 100%;
    @include respond-to(lg-1260) {
        padding: 32px;
    }
    @include respond-to(sm) {
        margin-bottom: 24px;
        height: auto;
    }
    &__header {
        padding-bottom: 8px;
    }
    &__body {
        height: calc(100% - 67px);
        display: flex;
        flex-direction: column;
    }
    &__title {
        padding-bottom: 4px;
        border-bottom: 2px solid;
        @include fsb;
    }
    &__icon {
        font-size: 12px;
        @include fm;
        img {
            max-width: 46px;
            margin: auto;
            @include respond-to(sm-767) {
                max-width: 40px;
            }
        }
    }
    &__price {
        font-size: 22px;
        line-height: 28px;
        @include respond-to(sm-767) {
            font-size: 18px;
            line-height: 22px;
        }
    }
    .price {
        font-size: 40px;
        line-height: 48px;
        @include fbl;
        @include respond-to(sm-767) {
            font-size: 30px;
            line-height: 36px;
        }
    }
    &__list {
        padding: 22px 0 32px;
        color: white;
        @include respond-to(sm-767) {
            padding: 16px 0;
        }
        li {
            position: relative;
            margin-bottom: 16px;
            padding-left: 20px;
            &::before {
                content: '';
                position: absolute;
                width: 4px;
                height: 4px;
                border-radius: 50%;
                background-color: white;
                top: 10px;
                left: 4px;
            }

        }
    }
    &__btns {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: auto;
        text-align: center;
        .btn__wrap {
            margin-bottom: 16px;
        }
    }
}

//team start
.team {
    background-color: $default-color;
    .page-title {
        padding-bottom: 0;
    }
    &__slider {
        .swiper{
            padding: 50px 0;
            @include respond-to(sm){
                padding: 22px 0;
                overflow: visible;
            }
        }
        .swiper-wrapper {
            @include respond-to(sm){
                flex-wrap: wrap;
            }
            .swiper-slide {
                @include respond-to(sm){
                    width: 50%;
                    padding: 0 16px 24px;
                    &:last-child {
                        padding-bottom: 0;
                    }
                }
                @include respond-to(xs-600){
                    width: 100%;
                    padding-left: 0;
                    padding-right: 0;
                }
            }
        }
    }
}
.team-member {
    &__item {
        cursor: pointer;
        height: 609px;
        min-height: 609px;
        perspective: 1000px;
        position: relative;
        @include respond-to(sm-767){
            height: 485px;
            min-height: 485px;
        }
        @include respond-to(sxs){
            height: 465px;
            min-height: 465px;
        }
        &:hover {
            .team-member__front {
                transform: rotateY(180deg);
            }
            .team-member__back {
                transform: rotateY(0deg);
            }
        }
    }
    &__photo {
        @include respond-to(sm-767){
            max-width: 150px;
        }
    }
    &__front,
    &__back {
        position: absolute;
        height: 100%;
        width: 100%;
        top: 0;
        left: 0;
        border-radius: 17px;
        backface-visibility: hidden;
        transform-style: preserve-3d;
        transition: ease-in-out 600ms;
        img {
            border-radius: 17px;
            width: 100%;
        }
    }
    &__front {
        overflow: hidden;
        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 180px;
            z-index: 1;
            background: linear-gradient(180deg, rgba(0, 0, 0, 0) 17.74%, rgba(0, 0, 0, 0.85) 69.83%);
        }
        img {
            height: 100%;
            object-fit: cover;
        }
    }
    &__back {
        transform: rotateY(-180deg);
        position: relative;
        background-image: url(../images/ornament.svg);
        background-color: $default-color;
        background-size: cover;
        border: 2px solid $main-blue;
        padding: 38px 34px;
        display: flex;
        flex-direction: column;
        @include respond-to(sm-767){
            padding: 28px;
        }
    }
    &__info {
        position: absolute;
        z-index: 2;
        bottom: 23px;
        left: 0;
        right: 0;
        padding: 0 30px;
    }
    &__name {
        @include respond-to(sm-767){
            padding-top: 8px;
        }
    }
    &__name,
    &__description {
        @include fsb;
    }
    &__achievs {
        padding-left: 34px;
        @include respond-to(sm-767){
           padding-left: 22px;
        }
    }
    &__achiev-item {
        .text-subheader {
            @include fsb;
        }
        &:last-child {
            .text-subheader {
                margin-bottom: 0;
            }
        }
    }
    &__list {
        margin-top: 22px;
        @include respond-to(sm-767){
            margin-top: 8px;
        }
        li {
            margin-bottom: 12px;
            @include respond-to(sm-767){
                margin-bottom: 8px;
            }
        }
    }
}
//steps start
.steps-bg {
    background-image: url(../images/step-bg.jpg);
    background-color: $main-blue;
    background-size: cover;
    background-attachment: fixed;
    &::after {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: rgba(137, 215, 215, 0.8);
    }
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: calc(50% + 3px);
        transform: translateX(-50%);
        width: 6px;
        height: 100%;
        border-left: 1px solid $default-color;
        z-index: 1;
        @include respond-to(md){
            content: none;
        }
    }
}
.step-row {
    display: flex;
    justify-content: flex-end;
    margin-top: -75px;
    flex-wrap: wrap;
    @include respond-to(md){
        margin-top: 24px;
    }
    &:nth-child(even) {
        justify-content: flex-start;
        .step-item__wrap  {
            border-left: none;
            border-right: 1px solid $default-color;
            padding-right: 114px;
            padding-left: 0;
            width: calc(50% + 1px);
            @include respond-to(md){
                width: 100%;
                padding-right: 0;
                border: none;
            }
        }
        .step-item__badge {
            left: auto;
            right: -60px;
        }
        .step-item__title {
            left: auto;
            right: 0;
            &::after {
                left: calc(100% - 15px);
                right: auto;
                box-shadow: 2px 0px 3px rgba(0, 0, 0, 0.1);
            }
            @include respond-to(md){
                right: auto;
                left: 20px;
            }
        }
    }
    &:first-child {
        padding-top: 75px;
        @include respond-to(md){
            margin-top: 0;
            padding-top: 0;
        }
    }
}
.step-descr {
    width: 50%;
    padding-right: 114px;
    @include respond-to(md){
        width: 100%;
        padding-right: 0;
    }
    @include respond-to(xs-600){
        font-size: 14px;
        font-weight: 600;
    }
}
.step-item {
    position: relative;
    border-radius: 17px;
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(360deg, rgba(0, 0, 0, 0.8) 45.19%, rgba(0, 0, 0, 0) 100%);
        border-radius: 17px;
    }
    &__info {
        position: absolute;
        bottom: 20px;
        z-index: 1;
        color: white;
        padding: 0 40px;
        text-align: center;
        @include respond-to(lg-1260) {
            padding: 0 20px;
        }
    }
    &__title {
        position: absolute;
        top: 30px;
        left: 0px;
        background: $main-blue;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
        font-size: 20px;
        line-height: 27px;
        @include fsb;
        padding: 8px 14px;
        @include respond-to(lg-1260) {
            font-size: 16px;
        }
        @include respond-to(md){
            top: 22px;
            left: 20px;
            padding: 0 12px;
        }
        span {
            position: relative;
            z-index: 1;
        }
        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
            background-color: $main-blue;
        }
        &::after {
            content: '';
            position: absolute;
            top: 7px;
            right: calc(100% - 15px);
            background: $main-blue;
            border: 15px solid transparent;
            transform: rotate(45deg);
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
            z-index: 0;
            @include respond-to(md){
                content: none;
            }
          }
    }
    &__wrap {
        position: relative;
        width: 50%;
        padding-left: 114px;
        border-left: 1px solid $default-color;
        @include respond-to(md){
            width: 100%;
            padding-left: 0;
            border: none;
        }
    }
    &__badge {
        background: $main-blue;
        border: 2px solid $default-color;
        border-radius: 8px;
        font-size: 16px;
        line-height: 22px;
        padding: 10px;
        width: 121px;
        text-align: center;
        @include fsb;
        position: absolute;
        left: -60px;
        top: 30px;
        @include respond-to(md){
            display: none;
        }
    }
    img {
        width: 100%;
        border-radius: 17px;
    }
}
.devider{
    background-color: $main-blue;
    height: 15px;
    @include respond-to(sm-767) {
        height: 6px;
    }
}