.icon {
	display: inline-block;
	width: 1em;
	height: 1em;
	fill: currentColor;
}
a {
  @include anim;
  color: $default-color;
  &:hover {
    color: $default-color;
  }
}
b, strong {
  font-weight: normal;
  @include fb;
}
*, *:after, *:before {
  box-sizing: border-box !important;
}
.input::-webkit-input-placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
  color: rgba(0, 0, 0, 0.25);
}
.input::-moz-placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
  color: rgba(0, 0, 0, 0.25);
}
.input:-ms-input-placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
  color: rgba(0, 0, 0, 0.25);
}
.clearfix:before, .container:before,
.clearfix:after, .container:after {
  display: table;
  content: " ";
}
.clearfix:after, .container:after {
  clear: both;
}
.desktop ::-webkit-scrollbar {
  width: 5px;
}
.desktop ::-webkit-scrollbar:horizontal {
  height: 5px;
}
.desktop ::-webkit-scrollbar-thumb {
  width: 5px;
  border-color: $main-blue;
  background-color: $main-blue;
}
.desktop ::-webkit-scrollbar-thumb:hover {
  border-width: 1px 1px 1px 2px;
  border-color: $main-blue;
  background-color: $main-blue;
}
.desktop ::-webkit-scrollbar-track {
  border-width: 0;
}
.desktop ::-webkit-scrollbar-track-piece {
  background: #a4a9ae;
}
input[type=tel] {
  padding-left: 100px !important;
}
body {
  max-width: 100%;
}
body.noscroll {
  overflow: hidden;
}
noscript {
  position: fixed;
  top: 0;
  background: #fff;
  font-size: 14px;
  width: 100%;
  z-index: 9999999;
  text-align: center;
}
noscript a {
  color: red;
  text-decoration: underline;
}
noscript a:hover {
  text-decoration: none;
}
.button-reset {
  border: 0;
  background: 0;
  padding: 0;
}
.bordered {
  border-bottom: 1px solid $main-blue;
}
.input-field {
  position: relative;
  margin-bottom: 20px;
  &:before {
    content: attr(data-error);
    position: absolute;
    top: calc(100% + 3px);
    left: 0;
    color: $main-red;
    font-size: 10px;
    line-height: 14px;
    @include fm;
    opacity: 0;
    visibility: hidden;
    @include anim;
    @include respond-to(sm) {
      top: 100%;
    }
  }
  &.error {
    input {
      border-color: $main-red;
    }
    &:before {
      visibility: visible;
      opacity: 1;
    }
  }
}

input {
  @include fr;
  margin: 0;
  background: transparent;
  border: 1px solid #89D7D7;
  border-radius: 8px;
  padding: 0 13px;
  box-shadow: none;
  width: 100%;
  height: 41px;
  font-size: 14px;
  line-height: 16px;
  color: white;
}

html {
  height: 100%;
  min-height: 100%;
  max-width: 100%;
  overflow: visible;
  &.main-navigation-mobile-is-open {
    overflow: hidden;
  }
}
.wrapper {
  display: table;
  height: 100%;
  width: 100%;
  table-layout: fixed;
  overflow: hidden;
  background-color: #f5f5f5;
  min-height: 100%;
  position: relative;
  max-width: 100%;
}
body {
  margin: 0;
  color: $default-color;
  height: 100%;
  min-height: 100%;
  max-width: 100%;
  background: #ffffff;
  overflow-x: hidden;
  font-size: 18px;
  line-height: 24px;
  @include fr;
}
.container {
  max-width: 1260px;
  width: 100%;
  padding: 0 16px;
  margin: auto;
  @include respond-to(lg-1360) {
    padding: 0 30px;
  }
  @include respond-to(sm-767) {
    padding: 0 20px;
  }
}
.row {
  margin-right: -15px;
  margin-left: -15px;
}
[class*="col-"] {
  padding-right: 15px;
  padding-left: 15px;
}
.full-sm {
  @include respond-to(sm) {
    width: 100%;
    max-width: 100%;
    flex-basis: 100%;
  }
}
.d-flex {
  display: flex;
}

.flex-wrap {
  box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 0;
  -ms-flex: 0 1 auto;
  flex: 0 1 auto;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}
.align-center {
  text-align: center;
}
.page-title {
  @include fbl;
  font-size: 40px;
  line-height: 48px;
  text-transform: uppercase;
  font-style: italic;
  color: $default-color;
  padding-bottom: 32px;
  @include respond-to(lg-1260) {
    font-size: 30px;
    line-height: 40px;
    padding-bottom: 22px;
  }
  @include respond-to(sm-767) {
    font-size: 18px;
    line-height: 24px;
    width: 100%;
    justify-content: space-between;
  }
  &--blue {
    color: $main-blue;
    .play-btn {
      border: 1px solid $main-blue;
    }
  }
}
.title-block {
  padding-bottom: 30px;
  @include respond-to(sm-767) {
    justify-content: center;
    padding-bottom: 20px;
  }
  .page-title {
    padding-bottom: 0;

  }
}
.dropdown-custom {
  opacity: 0;
  display: none;
  background: #fff;
  line-height: normal;
  width: 100%;
  position: absolute;
  top: 100%;
  left: 50%;
  right: 0;
  box-shadow: 0 0 10px rgba(0, 0, 0, .22);
  transform: translateX(-50%);
  transition: all .4s ease-out;
  min-width: 55px;
  text-align: center;
  border-radius: 8px;
  li {
    padding: 10px 5px;
    text-align: center;
    border-bottom: 1px solid #ececec;
    font-size: 14px;
    display: block;
    &:last-child {
      border-bottom: none;
    }
    &:hover {
      background-color: rgba(137, 215, 215, 0.2);
    }
  }
  &.visible {
    z-index: 55;
    visibility: visible;
    opacity: 1;
    display: block;
    transform: translateX(-50%)
  }
}
.header-drop__list {
  border-radius: 8px;
  overflow: hidden;
}
.btn {
  @include fsb;
  font-size: 16px;
  line-height: 18px;
  color: $default-color;
  text-align: center;
  background: $main-blue;
  border-radius: 8px;
  transition: all .3s ease;
  padding: 11px 30px;
  min-width: 160px;
  border: none;
  display: inline-block;
  z-index: 9;
  cursor: pointer;
  @include respond-to(lg-1260) {
    font-size: 14px;
  }
  @include respond-to(tablet-1030) {
    padding-left: 13px;
    padding-right: 13px;
  }
  @include respond-to(sm-767) {
    font-size: 12px;
    padding-top: 8px;
    padding-bottom: 8px;
  }
  &.disable {
    cursor: default;
    opacity: .5;
  }
  &__wrap {
    overflow: hidden;
    position: relative;
    display: inline-block;
    border-radius: 8px;
  }
  &__circle {
    background-color: $main-blue;
    border-radius: 50%;
    position: absolute;
    left:0;
    top:0;
    width: 0px;
    height: 0px;
    margin-left: 0px;
    margin-top: 0px;
    pointer-events: none;
  }
  &__text {
    position: relative;
    z-index: 9;
  }
}
.explode-circle {
  animation: explode 0.5s forwards;
}
.desplode-circle{
  animation: desplode 0.5s forwards;
}
@keyframes explode {
 0% {
   width: 0px;
   height: 0px;
   margin-left: 0px;
   margin-top: 0px;
   background-color: rgba(34, 61, 241, 0.2);
 }
 100% {
   width: 400px;
   height: 400px;
   margin-left: -200px;
   margin-top: -200px;
   background-color: rgba(255, 235, 59, 0.95);
 }
}
@keyframes desplode {
 0% {
   width: 400px;
   height: 400px;
   margin-left: -200px;
   margin-top: -200px;
   background-color: rgba(255, 235, 59, 0.95);
 }
 100% {
   width: 0px;
   height: 0px;
   margin-left: 0px;
   margin-top: 0px;
   background-color: rgba(137, 215, 215, 0.6);
 }
}
.go-to-top {
  position: fixed;
  bottom: 70px;
  right: 70px;
  z-index: 3;
  background-color: $main-blue;
  width: 40px;
  height: 40px;
  color: $default-color;
  cursor: pointer;
  opacity: 0;
  line-height: 40px;
  @include anim;
  text-align: center;
  border-radius: 8px;
  font-size: 15px;
  @include respond-to(lg) {
    right: 20px;

  }
  @include respond-to(sm) {
    right: 20px;
    bottom: 20px;
    width: 40px;
    height: 40px;
    line-height: 40px;
  }
}
.go-to-top .icon {
  font-size: 20px;
  transform: rotate(90deg);
  transform-origin: 40% 65%;
}
.go-to-top:hover {
  background-color: $yellow;
}
.go-to-top.act {
  opacity: 1
}
.soc-item,
.mainMenu .soc-item {
  font-size: 23px;
  margin-right: 22px;
  color: $main-blue;
  @include respond-to(sm-767){
    font-size: 18px;
  }
  &:hover, &:active, &:focus {
    color: $yellow;
  }
  &:last-child {
    margin-right: 0;
  }
}
.link {
  background-image: linear-gradient(
    to right,
    $yellow,
    $yellow 50%,
    $main-blue 50%
  );
  background-size: 200% 100%;
  background-position: -100%;
  display: inline-block;
  padding: 5px 0;
  position: relative;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: all 0.5s ease-in-out;
  &:hover {
    background-position: 0;
    &.bordered {
      border-color: $yellow;
    }
  }
}
.page-overlay:before {
  content: '';
  opacity: 0;
  -webkit-transition: all 0.75s cubic-bezier(0.77, 0.06, 0.28, 0.85);
  -o-transition: all 0.75s cubic-bezier(0.77, 0.06, 0.28, 0.85);
  transition: all 0.75s cubic-bezier(0.77, 0.06, 0.28, 0.85); }

.main-navigation-menu-is-open .page-overlay:before {
  content: '';
  position: fixed;
  z-index: 2222;
  top: 0;
  left: 0;
  opacity: 1;
  width: 100%;
  height: 100%;
  background-color: rgba(57, 57, 57, 0.5); }

.main-navigation-menu-is-open body {
  overflow: hidden; 
}
.iti__flag-container {
  width: 100%;
}
.iti__selected-flag {
  width: 90px;
  border-right: 1px solid rgba(137, 215, 215, 0.32);
}
.iti__selected-dial-code {
  margin-left: 0;
}
.iti__country-list {
  position: relative;
  max-width: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  background-color: $default-color;
}
.iti__flag-box {
  display: none;
}
@media (min-width: 1200px) { 
  .right-animate {
    opacity: 0;
    transition: all 1s cubic-bezier(.68, -.55, .65, 1.55);
    transform: translateX(100px)!important;
  }

  .right-animate.in-view {
    opacity: 1;
    transform: translateX(0)!important;
  }
  .left-animate {
    opacity: 0;
    transition:all 1s cubic-bezier(.68, -.55, .65, 1.55);
    transform: translateX(-100px)!important;
  }
  .left-animate.in-view {
    opacity: 1;
    transform: translateX(0)!important;
  }
  .totop-animate {
    opacity: 0;
    transition: all .8s cubic-bezier(.68, -.55, .65, 1.55);
    transform: translateY(50px);
  }

  .totop-animate.in-view {
    opacity: 1;
    transform: translateY(0);
  }
}