.modal {
  display: none;
  max-width: 416px;
  background: $default-color;
  text-align: center;
  color: white;
  @include fr;
  font-size: 14px;
  line-height: 20px;
  cursor: default!important;
  padding: 27px 33px 36px;
  outline: none;
  width:100%;
  border-radius: 18px;
  @include respond-to(xs){
    padding: 30px 15px 20px;
  }
  &-title {
    @include fsb;
    font-size: 24px;
    line-height: 34px;
    color: white;
    padding-bottom: 22px;
  }
  &-descr{
    @include fm;
    font-size: 16px;
    line-height: 20px;
    color: white;
    margin-bottom: 28px;
  }
  &:after {
    content: "";
    -webkit-mask-composite: xor;
    -webkit-mask-composite: xor;
    -webkit-mask-source-type: auto,auto;
    background: linear-gradient(#2b4aed,#ffe600);
    border-radius: 8px;
    padding: 2px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    -webkit-mask-image: linear-gradient(#fff 0,#fff 0),linear-gradient(#fff 0,#fff 0);
    -webkit-mask-position: 0 0,0 0;
    -webkit-mask-size: auto,auto;
    -webkit-mask-repeat: repeat,repeat;
    -webkit-mask-clip: content-box,border-box;
    -webkit-mask-origin: content-box,border-box;
    -webkit-mask-composite: xor;
    mask-composite: exclude;
    -webkit-mask-source-type: auto,auto;
    mask-mode: match-source,match-source;
    z-index: -1;
  }
  &.content-left {
    text-align: left;
    align-items: flex-start;
  }
}
.fancybox-overlay {
  background: rgba(57,57,57,.5);
  background-image: none;
}
.order-form {
  max-width: 243px;
  margin: auto;
}
.is-close {
  width: 16px;
  top: 8px !important;
  right: 16px !important;
  @include anim;
  &:hover{
    transform: rotate(180deg);
  }
}
.fancybox__content :focus:not(.carousel__button.is-close) {
  outline: none;
}
.iti__flag {
  background-image: url("../images/flags.png");
}
.modal--lg {
  max-width: 525px;
  .order-form {
    margin: 0;
    max-width: 100%;
  }
}