.footer {
  display: table-footer-group;
  color: $main-blue;
  background: $default-color;
  &__logo{
    margin-right: 70px;
    @include respond-to(lg-1260) {
      margin-right: 22px;
    }
    @include respond-to(sm){
      width: 100%;
      margin-right: 0;
      margin-bottom: 22px;
      img {
        margin: auto;
      }
    }
  }
  &__nav{
    width: 75%;
    padding: 50px 0;
    @include respond-to(md){
      width: 100%;
      justify-content: center;
      padding: 38px 0;
    }
    ul {
      @include respond-to(sm-767){
        flex-direction: column;
      }
    }
    &-item{
      @include fsb;
      font-size: 16px;
      line-height: 22px;
      letter-spacing: 0.02em;
      color: $main-blue;
      margin-right: 22px;
      display: inline-block;
      @include respond-to(lg-1260) {
        font-size: 14px;
        line-height: 20px;
      }
      @include respond-to(sm-767) {
        font-size: 12px;
        line-height: 18px;
        margin-right: 0;
        display: block;
      }
      &.dashed{
        border-bottom: 1px dashed;
      }
      &:hover{
        color: $yellow;
      }
    }
  }
  &__contact {
    width: 25%;
    padding-top: 50px;
    padding-bottom: 50px;
    padding-left: 28px;
    border-left: 1px solid $main-blue;
    @include respond-to(md){
      position: relative;
      width: 100%;
      border-left: none;
      padding-left: 0;
      text-align: center;
      padding-top: 22px;
      padding-bottom: 38px;
      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 1px;
        width: 103px;
        background-color: $main-blue;
        margin: auto;
      }
    }
    .footer__nav-item {
      @include respond-to(md){
        margin-right: 0;
      }
    }
  }
  &__soc {
    @include respond-to(md){
      justify-content: center;
    }
  }
}
