/*Variables*/

/*COLORS*/
$default-color: #000000;
$main-red: #F42D51;
$main-blue: #89D7D7;
$gray-bg: #EEEEEE;
$light-gray-bg: #D6D6D6;
$gray: #272727;
$yellow: rgba(255, 235, 59, 0.8);
$shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.18);
$error: red;

/*ANIMATIONS*/
@mixin anim {
  transition: all .2s ease;
}

@mixin anim-linear {
  transition: all .4s linear;
}

/*FONTS*/
@mixin fm {
  font-family: 'Montserrat-Medium', sans-serif;
}

@mixin fr {
  font-family: 'Montserrat-Regular', sans-serif;
}

@mixin fl {
  font-family: 'Montserrat-Light', sans-serif;
}

@mixin fb {
  font-family: 'Montserrat-Bold', sans-serif;
}

@mixin fbl {
  font-family: 'Montserrat-Black', sans-serif;
}

@mixin fsb {
  font-family: 'Montserrat-SemiBold', sans-serif;
}

@mixin ft {
  font-family: 'Montserrat-Thin', sans-serif;
}

/*=======================START-MEDIA=====================*/
@mixin respond-to($media) {
  @if $media == xs-320 {
    @media only screen and (max-width: 320px) {
      @content;
    }
  }
  @if $media == xs-480 {
    @media only screen and (max-width: 480px) {
      @content;
    }
  }
  @if $media == xs {
    @media only screen and (max-width: 570px) {
      @content;
    }
  }
  @if $media == xs-600 {
    @media only screen and (max-width: 600px) {
      @content;
    }
  } @else if $media == sm {
    @media only screen and (max-width: 768px) {
      @content;
    }
  } @else if $media == sm-767 {
    @media only screen and (max-width: 767px) {
      @content;
    }
  } @else if $media == sm-800 {
    @media only screen and (max-width: 800px) {
      @content;
    }
  } @else if $media == md-960 {
    @media only screen and (max-width: 960px) {
      @content;
    }
  } @else if $media == md {
    @media only screen and (max-width: 991px) {
      @content;
    }
  }
  @if $media == min-1030 {
    @media only screen and (min-width: 1030px) {
      @content;
    }
  } @else if $media == tablet {
    @media only screen and (max-width: 1023px) {
      @content;
    }
  } @else if $media == tablet-1030 {
    @media only screen and (max-width: 1030px) {
      @content;
    }
  } @else if $media == tablet-b {
    @media only screen and (max-width: 1180px) {
      @content;
    }
  } @else if $media == lg-1200 {
    @media only screen and (max-width: 1200px) {
      @content;
    }
  } @else if $media == lg-1260 {
    @media only screen and (max-width: 1260px) {
      @content;
    }
  } @else if $media == lg-1360 {
    @media only screen and (max-width: 1360px) {
      @content;
    }
  }@else if $media == lg-1366 {
    @media only screen and (max-width: 1366px) {
      @content;
    }
  } @else if $media == lg {
    @media only screen and (max-width: 1560px) {
      @content;
    }
  } @else if $media == lg-1700 {
    @media only screen and (max-width: 1700px) {
      @content;
    }
  } @else if $media == max-md {
    @media only screen and (max-width: 991px) {
      @content;
    }
  } @else if $media == max-lg {
    @media only screen and (max-width: 1560px) {
      @content;
    }
  } @else if $media == min-lg-1700 {
    @media only screen and (min-width: 1700px) {
      @content;
    }
  } @else if $media == max-sm-600 {
    @media only screen and (max-width: 600px) {
      @content;
    }
  }
}

/*=======================END-MEDIA=====================*/

